import { Storage } from '@ionic/storage';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Content } from 'ionic-angular';
import { NewsServiceProvider } from './../../providers/news-service/news-service';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';
import { News } from './../../models/news.interface';

/**
 * Generated class for the NewsFeedDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-news-feed-details',
  templateUrl: 'news-feed-details.html',
})
export class NewsFeedDetailsPage {
  
  data: any;

  @ViewChild(Content)
  content: Content;
  token: string;
  NewsFeed: News;
  cover: string = '';

  active: boolean;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private auth: AuthServiceProvider,
    private NewsProv: NewsServiceProvider,
    private storage: Storage
  ) {
    this.data = {
      "avatar": "assets/images/avatar/24.jpg",
      "shareIcon": "more",
    }
  }

  ionViewDidEnter() {
    let uid = this.navParams.get('uid');
    this.NewsFeed = undefined;
    this.NewsProv.get_by_uid(uid).subscribe((data) => {
      this.NewsFeed = data.data;
      this.cover = this.NewsFeed.main_image;
    },(error)=>this.navCtrl.setRoot('HomePage'),()=>console.log('Okay'))
  }

  onStarClass(items: any, index: number, e: any) {
    for (var i = 0; i < items.length; i++) {
      items[i].isActive = i <= index;
    }
    
  }

  ngAfterViewInit() {
    this.subscribeToIonScroll();
  }

  isClassActive() {
    return this.active;
  }

  subscribeToIonScroll() {
    if (this.content != null && this.content.ionScroll != null) {
      this.content.ionScroll.subscribe((d) => {
        if (d.scrollTop < 200) {
          this.active = false;
          return;
        }
        this.active = true;
      });
    }
  }

}



